
import { getMyTicketist } from '@/api/user.js'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import noData from '@/components/noData.vue'
export default {
name: 'loginRegister',
components: {
Tab,
TabItem,
noData
},
data () {
return {
stroke: 'static/images/stroke.png',
strokestaus: 0,
isShow: false,
showlist: false,
canusenum: '',
notusenum: '',

curid: '',
canuselist: [],
notuselist: [],
listData: {'img': 'static/images/stroke.png', 'text': '您还没领到优惠券哦~'}
}
},
methods: {
chiose: function () {
if (this.strokestaus === 0) {
this.stroke = 'static/images/strokeCur.png'
this.strokestaus = 1
} else {
this.stroke = 'static/images/stroke.png'
this.strokestaus = 0
}
},
showitem (index) {





this.showlist = !this.showlist
},
couponRuleMain: function () {
this.isShow = !this.isShow
},
myTicketist () {
const postData = {}
const that = this
getMyTicketist(postData)
.then(res => {
if (res.data.errorcode === 0) {
that.canuselist = res.data.data.canuselist
that.notuselist = res.data.data.notuselist
console.log(res.data.data.canuselist.length)
if (res.data.data.canuselist.length === 0) {
this.canusenum = '无'
} else {
this.canusenum = res.data.data.canuselist.length
}
if (res.data.data.notuselist.length === 0) {
this.notusenum = '无'
} else {
this.notusenum = res.data.data.notuselist.length
}
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('网络异常！', 'middle')
})
}
},
created () {
this.myTicketist()
}
}



